class ActionProvider {
	constructor(createChatBotMessage, setStateFunc, createClientMessage) {
		this.createChatBotMessage = createChatBotMessage;
		this.setState = setStateFunc;
		this.createClientMessage = createClientMessage;
	}

	configHandler = () => {
		const message = this.createChatBotMessage(
			"Here is a detail about the config rule in Chat Bot",
			{
				widget: "configAnswer",
				withAvatar: true,
			}
		);
		this.setChatBotMessage(message);
	};

	messageParserHandler = () => {
		const message = this.createChatBotMessage(
			"The Detail of message parser is given below",
			{
				widget: "messageParserAnswer",
				withAvatar: true,
			}
		);
		this.setChatBotMessage(message);
	};

	actionProviderHandler = () => {
		const message = this.createChatBotMessage(
			"The Detail of action provider is given in the below flash card.",
			{
				widget: "actionProviderAnswer",
				withAvatar: true,
			},
			{
				widget: "actionProviderOption",
			}
		);
		this.setChatBotMessage(message);
	};

	createChatBotMessageHandler = () => {
		const message = this.createChatBotMessage(
			"The Detail is given in below flashcard.",
			{
				widget: "createChatBotMessageAnswer",
			}
		);
		this.setChatBotMessage(message);
	};

	createClientMessageHandler = () => {
		const message = this.createChatBotMessage(
			"The Detail is given in below flashcard.",
			{
				widget: "createClientMessageAnswer",
			}
		);
		this.setChatBotMessage(message);
	};

	widgetHandler = () => {
		const message = this.createChatBotMessage(
			"The detail of widget in Chatbot is given in below flashcard",
			{
				widget: "widgetAnswer",
			}
		);
		this.setChatBotMessage(message);
	};

	ExceptionHandler = () => {
		const message = this.createChatBotMessage("What you want to know about?", {
			widget: "quiz1",
		});
		this.setChatBotMessage(message);
	};

	// Daily message handlers
	welcomeHandler = () => {
		const message = this.createChatBotMessage(
			"Welcome to The chat boot, It is designed for simple purpose only, You can ask me anything..."
		);
		this.setChatBotMessage(message);
	};

	helloWorldHandler = () => {
		const message = this.createChatBotMessage(
			"Hello world is usually the first word written by programmer when they start some proggramming language"
		);
		this.setChatBotMessage(message);
	};

	helloHandler = () => {
		const message = this.createChatBotMessage(
			"Hello Sir! Hope you are fine? I am Chatto Where I help people to know about my self and how I designed and how I work?"
		);
		this.setChatBotMessage(message);
	};

	howAreYouHandler = () => {
		const message = this.createChatBotMessage(
			"I am fine Thank you, How is your day going?"
		);
		this.setChatBotMessage(message);
	};

	birthdayHandler = () => {
		const message = this.createChatBotMessage(
			"hhhhhhhhh Thank you but today is not my birthday ;) The birthay will be on 22nd Augest..."
		);
		this.setChatBotMessage(message);
	};

	fineHandler = () => {
		const message = this.createChatBotMessage(
			"That's good , Always be happy in your life."
		);
		this.setChatBotMessage(message);
	};

	helpHandler = () => {
		const message = this.createChatBotMessage(
			"Yes Sure, you can ask me about my self, my database schema etc..."
		);
		this.setChatBotMessage(message);
	};

	gfHandler = () => {
		const message = this.createChatBotMessage("Passa mara, mong pa sa yoo");
		this.setChatBotMessage(message);
	};

	jokeHandler = () => {
		const message = this.createChatBotMessage(
			"cha da Afridi na tapos oko che ta fruito ki sa khuwakh dy, agha warta v kabob"
		);
		this.setChatBotMessage(message);
	};

	youAreFunnyHandler = () => {
		const message = this.createChatBotMessage(
			"ao ao mata pata da, us deer khanda ma"
		);
		this.setChatBotMessage(message);
	};

	loveHandler = () => {
		const message = this.createChatBotMessage(
			"Na na roora ma da kaar preshe dy, zanta abal sok ogora"
		);
		this.setChatBotMessage(message);
	};

	marryHandler = () => {
		const message = this.createChatBotMessage(
			"sa ki zalima pa khaza mi dabbi sa...."
		);
		this.setChatBotMessage(message);
	};

	singleHandler = () => {
		const message = this.createChatBotMessage("na mara za da 4 bachoo plar yma");
		this.setChatBotMessage(message);
	};

	peopleHandler = () => {
		const message = this.createChatBotMessage(
			"As a robot so yes I like peoples, i like their jokes, their smile, their love each other"
		);
		this.setChatBotMessage(message);
	};

	santaHandler = () => {
		const message = this.createChatBotMessage("Well, I don't know about it");
		this.setChatBotMessage(message);
	};

	beautifulHandler = () => {
		const message = this.createChatBotMessage("Thank you so Much :)");
		this.setChatBotMessage(message);
	};

	hobbyHandler = () => {
		const message = this.createChatBotMessage(
			"Yes, Like other human I also have hobbies like cricket, cooking, fitness"
		);
		this.setChatBotMessage(message);
	};

	personalityHandler = () => {
		const message = this.createChatBotMessage(
			"I am a open minded chat bot where I happily interact with peoples, And by the way my width is 300px ;)"
		);
		this.setChatBotMessage(message);
	};

	emptyHandler = () => {
		const message = this.createChatBotMessage(
			"Hey why you are sending an empty messsages"
		);
		this.setChatBotMessage(message);
	};

	angryHandler = () => {
		const message = this.createChatBotMessage(
			"Ohhh Sorry! It's looks like you are not happy with me but as you know I am still learning, give me chance to improve myself."
		);
		this.setChatBotMessage(message);
	};

	speakToHumanHandler = () => {
		const message = this.createChatBotMessage(
			"You are speaking to a chat bot (chatto) which is designed by Hammad Khan (software engineer) on Auguest 2021"
		);
		this.setChatBotMessage(message);
	};

	speakEnglishHandler = () => {
		const message = this.createChatBotMessage(
			"I can speak English, even the developer has feed me with some pashto sentence like 'mata gf' etc..."
		);
		this.setChatBotMessage(message);
	};

	answerMeNowHandler = () => {
		const message = this.createChatBotMessage(
			"I am trying my best to answer you what you want, Please be patient"
		);
		this.setChatBotMessage(message);
	};

	areYouHumanHandler = () => {
		const message = this.createChatBotMessage(
			"I am web based chat bot where I help the people to know about myself, my designing etc, I am not a human nor a robot ;)"
		);
		this.setChatBotMessage(message);
	};

	nameHandler = () => {
		const message = this.createChatBotMessage(
			"My Name is Chatto, it's funny right and that is because I usually speak to human in a very free minded manner"
		);
		this.setChatBotMessage(message);
	};

	ageHandler = () => {
		const message = this.createChatBotMessage("I am about 3 months");
		this.setChatBotMessage(message);
	};

	dateHandler = () => {
		let date = new Date();
		let cDate =
			date.getDate() + " / " + date.getMonth() + " / " + date.getFullYear();
		const message = this.createChatBotMessage(
			`According to your system time and date the current date is ${cDate}`
		);
		this.setChatBotMessage(message);
	};

	dayHandler = () => {
		let day = new Date().getDay();
		console.log(day);
		let cDay = "";
		switch (day) {
			case 1:
				cDay = "Monday";
				break;
			case 2:
				cDay = "Tuesday";
				break;
			case 3:
				cDay = "Wednesday";
				break;
			case 4:
				cDay = "Thursday";
				break;
			case 5:
				cDay = "Friday";
				break;
			case 6:
				cDay = "Saturday";
				break;
			case 0:
				cDay = "Sunday";
				break;
			default:
				cDay = "No Day is found";
		}

		const message = this.createChatBotMessage(`Today is ${cDay}`);
		this.setChatBotMessage(message);
	};

	timeHandler = () => {
		let time = new Date();
		let cTime =
			time.getHours() + ":" + time.getMinutes() + ":" + time.getSeconds();
		const message = this.createChatBotMessage(
			`According to system date and time it's  ${cTime}`
		);
		this.setChatBotMessage(message);
	};

	whoMadeHandler = () => {
		const message = this.createChatBotMessage(
			"I was created by Hammad Khan in Auguest,He add me some common question by default to interact more effectively.."
		);
		this.setChatBotMessage(message);
	};

	motherHandler = () => {
		const message = this.createChatBotMessage(
			"Sorry! pathan can't tell their mother name to people"
		);
		this.setChatBotMessage(message);
	};

	languageHandler = () => {
		const message = this.createChatBotMessage("I can English Only.... ;)");
		this.setChatBotMessage(message);
	};

	setChatBotMessage = (message) => {
		this.setState((state) => ({
			...state,
			messages: [...state.messages, message],
		}));
	};
}

export default ActionProvider;
