import React, { useState } from "react";

import "./PageStyle.css";
import Login from "../Components/Authentication/Login";

const Home = () => {
	// states
	const [isShow, setIsShow] = useState(false);

	// return
	return (
		<div className="home-div">
			<div className="desc-div">
				<p>
					Chatto is a complete web chatbot which help user to know about it's working
					and answers some daily questions
				</p>
				<p>Want to Start chatbot click Now...</p>
				<button className="btn" onClick={() => setIsShow(true)}>
					Start Now
				</button>
			</div>
			<div className="main-div">{isShow ? <Login /> : ""}</div>
		</div>
	);
};

export default Home;
