import { createChatBotMessage } from "react-chatbot-kit";
import Answers from "../API/Answers";
import AvatarComp from "../ChatBotAvatar/AvatarComp";
import ProviderQuiz from "../Tutorial/ProviderQuiz";
import Quiz1 from "../Tutorial/Quiz1";

const config = {
	initialMessages: [
		createChatBotMessage(`Hello I am chatto, how can I help you?`),
		createChatBotMessage(
			"Here's a quick overview over what I need to function. ask me about the different parts to dive deeper.",
			{
				delay: 900,
				withAvatar: false,
				widget: "quiz1",
			}
		),
	],
	botName: "Chatto",
	customComponents: {
		botAvatar: (props) => <AvatarComp {...props} />,
	},
	customStyles: {
		botMessageBox: {
			backgroundColor: "purple",
		},
		chatButton: {
			backgroundColor: "purple",
		},
	},
	state: {
		// botAnswer: [],
	},
	widgets: [
		{
			widgetName: "quiz1",
			widgetFunc: (props) => <Quiz1 {...props} />,
			// mapStateToProps: ["quiz"],
		},
		{
			widgetName: "configAnswer",
			widgetFunc: (props) => <Answers {...props} />,
			props: {
				text: "config",
			},
		},
		{
			widgetName: "messageParserAnswer",
			widgetFunc: (props) => <Answers {...props} />,
			props: {
				text: "message parser",
			},
		},
		{
			widgetName: "actionProviderOption",
			widgetFunc: (props) => <ProviderQuiz {...props} />,
		},
		{
			widgetName: "actionProviderAnswer",
			widgetFunc: (props) => <Answers {...props} />,
			props: {
				text: "action provider",
			},
		},
		{
			widgetName: "createChatBotMessageAnswer",
			widgetFunc: (props) => <Answers {...props} />,
			props: {
				text: "createchatbotmessage",
			},
		},
		{
			widgetName: "widgetAnswer",
			widgetFunc: (props) => <Answers {...props} />,
			props: {
				text: "widget",
			},
		},
		{
			widgetName: "createClientMessageAnswer",
			widgetFunc: (props) => <Answers {...props} />,
			props: {
				text: "createclientmessage",
			},
		},
	],
};

export default config;
