import React, { useEffect, useState } from "react";

import "./answerStyle.css";

const Answers = (props) => {
	// states
	// const { setState } = props;
	console.log(props.text);
	const [answer, setAnswer] = useState([]);

	// Function
	useEffect(async () => {
		await fetch(`http://localhost:8080/answer/getAnswer/${props.text}`)
			.then((res) => res.json())
			.then((data) => {
				console.log(data);
				setAnswer(data);
			})
			.catch((err) => alert(err));
	}, []);

	// const renderUsers = () => {
	// 	return props.answers.map((value, index) => {
	// 		return <p key={index}>{value.answer}</p>;
	// 	});
	// };

	// return
	return (
		<div className="answerFlashCard">
			{answer.map((ans, index) => {
				return <p key={index}>{ans[1]}</p>;
			})}
		</div>
	);
};

export default Answers;
