import React from "react";
import "./QuizStyle.css";

const ProviderQuiz = (props) => {
	const options = [
		{
			id: 1,
			handler: () => {
				props.ActionProvider.createChatBotMessageHandler();
			},
			text: "CreateChatBotMessage",
		},
		{
			id: 2,
			handler: () => {
				props.ActionProvider.createClientMessageHandler();
			},
			text: "CreateClientMessage",
		},
	];

	return (
		<div className="qst_div">
			{options.map((option) => {
				return (
					<button key={option.id} className="qst_btn" onClick={option.handler}>
						{option.text}
					</button>
				);
			})}
		</div>
	);
};

export default ProviderQuiz;
