class MessageParser {
	constructor(actionProvider, state) {
		this.actionProvider = actionProvider;
		this.state = state;
	}

	parse(message) {
		const userMessage = message.toLowerCase();
		console.log(userMessage);

		if (userMessage.includes("config")) {
			this.actionProvider.configHandler();
		} else if (
			userMessage.includes("messageparser") ||
			userMessage.includes("parser")
		) {
			this.actionProvider.messageParserHandler();
		} else if (
			userMessage.includes("action provider") ||
			userMessage.includes("actionprovider")
		) {
			this.actionProvider.actionProviderHandler();
		} else if (
			userMessage.includes("createchatbotmessage") ||
			userMessage.includes("create chat bot message")
		) {
			this.actionProvider.createChatBotMessageHandler();
		} else if (
			userMessage.includes("createclientmessage") ||
			userMessage.includes("create client message")
		) {
			this.actionProvider.createClientMessageHandler();
		} else if (userMessage.includes("widget")) {
			this.actionProvider.widgetHandler();
		}

		// differnt possible messages
		else if (userMessage.length < 1 || userMessage === null) {
			this.actionProvider.emptyHandler();
		} else if (
			userMessage.includes("what is your name") ||
			userMessage.includes("your name")
		) {
			this.actionProvider.nameHandler();
		} else if (
			userMessage.includes("how old are you") ||
			userMessage.includes("what is your age") ||
			userMessage.includes("you age")
		) {
			this.actionProvider.ageHandler();
		} else if (
			userMessage.includes("what date is today") ||
			userMessage.includes("what is the date") ||
			userMessage.includes("tell me current date") ||
			userMessage.includes("what is date")
		) {
			this.actionProvider.dateHandler();
		} else if (
			userMessage.includes("what day is today") ||
			userMessage.includes("today") ||
			userMessage.includes("today?") ||
			userMessage.includes("what day")
		) {
			this.actionProvider.dayHandler();
		} else if (
			userMessage.includes("what is the time") ||
			userMessage.includes("what time") ||
			userMessage.includes("time")
		) {
			this.actionProvider.timeHandler();
		} else if (
			userMessage.includes("who made you") ||
			userMessage.includes("who is your owner") ||
			userMessage.includes("your owner") ||
			userMessage.includes("your boss") ||
			userMessage.includes("who is your boss")
		) {
			this.actionProvider.whoMadeHandler();
		} else if (
			userMessage.includes("how many languages can you speak") ||
			userMessage.includes("your language") ||
			userMessage.includes("are you speak english")
		) {
			this.actionProvider.languageProvider();
		} else if (
			userMessage.includes("mother name") ||
			userMessage.includes("what is your mother name") ||
			userMessage.includes("what is your mother name")
		) {
			this.actionProvider.motherHandler();
		} else if (userMessage.includes("welcome")) {
			this.actionProvider.welcomeHandler();
		} else if (
			userMessage.includes("hello world") ||
			userMessage.includes("what is hello world")
		) {
			this.actionProvider.helloWorldHandler();
		} else if (
			userMessage.includes("hello") ||
			userMessage.includes("hey") ||
			userMessage.includes("tell me something") ||
			userMessage.includes("how can you help me") ||
			userMessage.includes("what can you do")
		) {
			this.actionProvider.helloHandler();
		} else if (
			userMessage.includes("how are you") ||
			userMessage.includes("how do you do") ||
			userMessage.includes("how are you doing") ||
			userMessage.includes("how are you going") ||
			userMessage.includes("whatâ€™s up") ||
			userMessage.includes("hi, my name is")
		) {
			this.actionProvider.howAreYouHandler();
		} else if (
			userMessage.includes("happy birthday") ||
			userMessage.includes("birthday to you") ||
			userMessage.includes("birthday 2 u")
		) {
			this.actionProvider.birthdayHandler();
		} else if (
			userMessage.includes("i am fine") ||
			userMessage.includes("i am fine thank you") ||
			userMessage.includes("i m fine thank u")
		) {
			this.actionProvider.fineHandler();
		} else if (
			userMessage.includes("i have a question") ||
			userMessage.includes("can you help me ") ||
			userMessage.includes("help me")
		) {
			this.actionProvider.helpHandler();
		} else if (
			userMessage.includes("i want gf") ||
			userMessage.includes("mata gf")
		) {
			this.actionProvider.gfHandler();
		} else if (
			userMessage.includes("tell me a joke") ||
			userMessage.includes("make me laugh") ||
			userMessage.includes("omi khandoo")
		) {
			this.actionProvider.jokeHandler();
		} else if (
			userMessage.includes("you are funny") ||
			userMessage.includes("u r funny")
		) {
			this.actionProvider.youAreFunnyHandler();
		} else if (
			userMessage.includes("do you love me") ||
			userMessage.includes("do u love m") ||
			userMessage.includes("i love you") ||
			userMessage.includes("love you")
		) {
			this.actionProvider.loveHandler();
		} else if (
			userMessage.includes("will you marray me") ||
			userMessage.includes("marry me") ||
			userMessage.includes("wada rasara oka") ||
			userMessage.includes("let's marry")
		) {
			this.actionProvider.marryHandler();
		} else if (
			userMessage.includes("are you a single") ||
			userMessage.includes("r u sigle") ||
			userMessage.includes("single e") ||
			userMessage.includes("are you single")
		) {
			this.actionProvider.singleHandler();
		} else if (userMessage.includes("do you like peoples")) {
			this.actionProvider.peopleHandler();
		} else if (
			userMessage.includes("does santa claus exists") ||
			userMessage.includes("santa claus")
		) {
			this.actionProvider.santaHandler();
		} else if (
			userMessage.includes("you are beautiful") ||
			userMessage.includes("you are handsome") ||
			userMessage.includes("you are cute") ||
			userMessage.includes("you are smart") ||
			userMessage.includes("you are intellegent") ||
			userMessage.includes("you are clever")
		) {
			this.actionProvider.beautifulHandler();
		} else if (
			userMessage.includes("what are your hobbies") ||
			userMessage.includes("your hobbies") ||
			userMessage.includes("what is your hobbies") ||
			userMessage.includes("do you have hobbies") ||
			userMessage.includes("do you have hobby")
		) {
			this.actionProvider.hobbyHandler();
		} else if (
			userMessage.includes("tell me about your personality") ||
			userMessage.includes("your personality plz")
		) {
			this.actionProvider.personalityHandler();
		} else if (
			userMessage.includes("you are bad") ||
			userMessage.includes("you are annoying") ||
			userMessage.includes("you suck") ||
			userMessage.includes("you are crazy") ||
			userMessage.includes("you are boring")
		) {
			this.actionProvider.angryHandler();
		} else if (
			userMessage.includes("i want ot speak to a human") ||
			userMessage.includes("I want to speak to a live agent") ||
			userMessage.includes("i want to speak to a customer service") ||
			userMessage.includes("are you a live agent") ||
			userMessage.includes("are you a human")
		) {
			this.actionProvider.speakToHumanHandler();
		} else if (
			userMessage.includes("don't you speak english") ||
			userMessage.includes("do you speak english") ||
			userMessage.includes("do you know english")
		) {
			this.actionProvider.speakEnglishHandler();
		} else if (
			userMessage.includes("answer me now") ||
			userMessage.includes("i want the answer now")
		) {
			this.actionProvider.answerMeNowHandler();
		} else if (
			userMessage.includes("are you a humman") ||
			userMessage.includes("are you human") ||
			userMessage.includes("are you robot") ||
			userMessage.includes("who are you") ||
			userMessage.includes("what are you doing")
		) {
			this.actionProvider.areYouHumanHandler();
		} else {
			this.actionProvider.ExceptionHandler();
		}
	}
}

export default MessageParser;
