import React from "react";

import "./QuizStyle.css";

const Quiz1 = (props) => {
	const options = [
		{
			id: 1,
			text: "Config",
			handler: () => {
				props.actionProvider.configHandler();
			},
		},
		{
			id: 2,
			text: "Message Parser",
			handler: () => {
				props.actionProvider.messageParserHandler();
			},
		},
		{
			id: 3,
			text: "Action Provider",
			handler: () => {
				props.actionProvider.actionProviderHandler();
			},
		},
		{
			id: 4,
			text: "widget",
			handler: () => {
				props.actionProvider.widgetHandler();
			},
		},
	];

	return (
		<div className="qst_div">
			{options.map((option) => {
				return (
					<button key={option.id} className="qst_btn" onClick={option.handler}>
						{option.text}
					</button>
				);
			})}
		</div>
	);
};

export default Quiz1;
