import React from "react";
import { TutorailPageData } from "./TutorailPageData";

const Tutorial = () => {
	return (
		<>
			{TutorailPageData.map((data) => {
				return (
					<div className="tutroail-div" key={data.id}>
						<h1 className="tut-title">{data.title}</h1>
						<p className="tut-desc">{data.desc}</p>
					</div>
				);
			})}
		</>
	);
};

export default Tutorial;
