import React, { useState } from "react";

const SignUp = () => {
	// states
	const [fullname, setFullname] = useState("");
	const [username, setUsername] = useState("");
	const [password, setPassword] = useState("");
	const [isValid, setIsValid] = useState(true);

	const formData = {
		userFullName: `${fullname}`,
		username: `${username}`,
		userPassword: `${password}`,
	};

	// function
	const signupToChat = async (event) => {
		event.preventDefault();

		if (fullname == "" || username == "" || password == "") {
			alert("please correctly fill out the form");
		} else {
			await fetch(`http://localhost:8080/user/newUser`, {
				method: "POST",
				headers: {
					"Content-Type": "application/json",
				},
				body: JSON.stringify(formData),
			})
				.then((res) => res.json())
				.then(() => setIsValid(false))
				.catch((err) => alert(err));
		}
	};

	// return
	return (
		<>
			{isValid ? (
				<div className="signup-div">
					<h1>Sign Up Here</h1>
					<form action="" className="signup-form">
						<input
							type="text"
							placeholder="Enter your full Name"
							className="signup-input-field"
							onChange={(e) => setFullname(e.target.value)}
							required
						/>
						<br />
						<input
							type="text"
							placeholder="Enter your username"
							className="signup-input-field"
							onChange={(e) => setUsername(e.target.value)}
							required
						/>
						<br />
						<input
							type="password"
							placeholder="Enter Password"
							className="signup-input-field"
							onChange={(e) => setPassword(e.target.value)}
							required
						/>
						<br />
						<input
							type="submit"
							value="Sign up"
							className="signup-btn"
							onClick={signupToChat}
						/>
					</form>
				</div>
			) : (
				<span style={{ color: "green", fontSize: "70px", textAlign: "center" }}>
					Record Added Successfully
				</span>
			)}
		</>
	);
};

export default SignUp;
